
<template>
  <div class="takePart">
		<div class="ruleExplain">
			<div class="reject" @click="reject" v-if="isOutSide !== true">
				<img src="../assets/close.png" alt="">
			</div>
			<h2 class="white">参与活动</h2>
			<div class="content green">
				<div class="activityBrief">
					<span class="strong">官服玩家</span><br>
					<span>官服玩家可在游戏内节日界面分享助力H5至微信或大神，即可参加活动</span>
				</div>
				
				<div class="btn" @click="openGame">
					<div>打开游戏</div>
				</div>
				<div class="activityTime">
					<span class="strong">渠道服玩家</span><br>
					<span>渠道服玩家请前往网易大神App，搜索“梦幻手游精灵”，绑定角色ID后接可参与活动</span>
				</div>
					<div class="btn" @click="openGodlike">
						<!-- <img src="../assets/btn.png" alt=""> -->
						<div class="goDashen">去大神参与活动</div>
					</div>
			</div>
		</div>	
	</div>
</template>
<script>

export default {
	name: 'takePart',
	props: ['windowOpen', 'isOutSide'],
	methods: {
		openGame(){
			window.location.href = 'https://game.163.com/open/my/'
		},
		openGodlike(){
			window.location.href = 'https://app.16163.com/ds/ulinks/?action=openUrl&url=https%3A%2F%2Fyouxi.dev.16163.com%2F19502b735b31c6d5'
		},
		reject() {
			this.$emit('update:windowOpen', false);
		}
	}
}
</script>

<style lang='less' scoped>
	.takePart {
		border-top: 1px solid transparent;
		.white {
			color: white;
		}
		.green {
			color: #829a7a;
		}
		.strong {
			font-weight: 700
		}
		.ruleExplain {
			position: relative;
			.reject {
				position: absolute;
				left: 50%;
				top: 1.01rem;
				transform: translateX(-50%);
				padding: 10px 20px;
				border-radius: 30px;
				width: 20%;
				> img {
					width: 100%;
				}
			}
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 80%;
			margin: 0 auto;
			margin-top: .18rem;
			background: url('../assets/background.png') no-repeat;
			border: 1px solid transparent;
			background-size: 100%;
			> h2 {
				margin-top: .035rem;
    		font-size: 1em;
			}
			>.content {
				text-align: left;
				width: 80%;
				margin: 0.05rem auto;
				font-size: .7em;
				// line-height: 50px;
				> .btn {
					text-align: center;
					margin: .05rem auto;
					position: relative;
					img {
						display: inline-block;
						width: 56%;
					}
					> div {
						display: inline-block;
						background: url('../assets/btn.png') no-repeat;
						margin: 0 auto;
						padding: .03rem .08rem;
						font-size: 1.3em;
						color: white;
						font-weight: 700;
						background-size: 100% 100%
					}
					> .goDashen {
						display: inline-block;
						background: url('../assets/btn.png') no-repeat;
						margin: 0 auto;
						padding: .04rem .08rem;
						font-size: 1.3em;
						color: white;
						font-weight: 700;
						background-size: 100% 100%
					}
				}
			}
		}
		
	}
</style>



