import bgBottom from '../assets/bg-bottom.jpg'


export default {
	name: 'HelloWorld',
	components: {
    swiper,
		swiperSlide,
		sucessPage,
		congratulate,
		takePart,
		rule
	},
	watch: {
		
	},
  data () {
    return {
			count: 0,
			progWidth: 0,
			homePageFlag: false,
			head: '',//测试
			// newScroll: newScroll,
			// machine: machine,
			isOutSide: '',//判断是否外部界面
			windowOpen: false,
			windowName: '',
			pictureSrc: 'http://yxzs.163.com/appresource/g18/',
			swiperCard:[
				{
					id: 1,
					name: '随机羊角包头饰',
					integral: '50',
					src: pic50,
					score: 50
				}, {
					id: 2,
					name: '50000银币',
					integral: '100',
					src: pic100,
					score: 100
				}, {
					id: 3,
					name: '翠羽金屏头像框',
					integral: '200',
					src: pic200,
					score: 200
				}, {
					id: 4,
					name: '北溟鲲转盘',
					integral: '300',
					src: pic300,
					score: 300
				}, {
					id: 5,
					name: '北溟鲲转盘',
					integral: '400',
					src: pic300,
					score: 400
				}, {
					id: 6,
					name: '北溟鲲转盘',
					integral: '500',
					src: pic300,
					score: 500
				}
			],
			swiperOption: {
				// some swiper options/callbacks
				// 所有的参数同 swiper 官方 api 参数
				// ...
				observeParents:true,
				autoplay:true,
				autoplay: {
        	disableOnInteraction: false
   			},
				loop: true, // 循环模式选项
				slidesPerView : 3,
				// autoplay: {
        // 	disableOnInteraction: false
   			// },
				// slidesPerview:'auto',
				on: {
					click: (e) => {
						let swiper = this.$refs.mySwiper.swiper;
						let integral =swiper.clickedSlide.getElementsByClassName('integral')[0].innerHTML;
						if(integral === '50积分') {
							this.congratulateParise = this.swiperCard[0]
						} else if(integral === '100积分') {
							this.congratulateParise = this.swiperCard[1]
						} else if(integral === '200积分') {
							this.congratulateParise = this.swiperCard[2]
						} else if(integral  === '300积分') {
							this.congratulateParise = this.swiperCard[3]
						} else if(integral  === '400积分') {
							this.congratulateParise = this.swiperCard[4]
						} else if(integral  === '500积分') {
							this.congratulateParise = this.swiperCard[5]
						}
						this.windowOpen = true;
						this.windowName = 'congratulate'
					},
				}
			},
			swiperOption2: {
				direction : "vertical",
				autoplay: true,
				loop: true,
				centeredSlides: true,
			},
			role_id: '',
			host:'',
			token: '',
			token_type: '',
			roleData: null, //角色数据
			show_img: '',
			user_image: '',
			role_name: '',
			friendArr:[],
			broadcast: [],//轮播图
			// broadcastPic: '', //轮播图头像
			points: '',//分数
			helpPoints: '',//助力的分数
			highPointsAchieve: '', //最高达成的奖励
			restPoint: '', //剩余分数
			congratulateParise: {},
			shareParam: '',
		// 初始化分享组件
			mobileShare: ''
    }
	},
	computed: {
		swiper() {
			return this.$refs.mySwiper.swiper
		},
		swiper2() {
