				margin-top: 50%;
				transform: translateY(-52%)
			}
		}
		.bgBack {
			font-size: .7em;
			margin-top: 0.3rem;
			border-top: 1px solid transparent;
			height: 1.055rem;
			div:nth-child(1) {
				margin-top: 0.035rem;
				color: #16442b;
				font-weight: 700
			}
		}
		.bgGreen {
			background-color: #16442b;
			border-top: 1px solid #16442b;
			border-bottom: 1px solid transparent;
			.nickName{
				font-size: .7em;
				background-color: #255e3f;
				width: 90%;
				margin: .05rem auto;
				display: flex;
				display: -webkit-flex;
				justify-content: center;
				align-items:center;
				border-radius: 15px;
				overflow: hidden;
				> img:nth-child(1) {
					width: 5%;
					padding: 0 .01rem;
				}
				.nameInfo {
					display: flex;
					display: -webkit-flex;
					// justify-content: center;
					align-items:center;
					text-align: left;
					margin-top: .01rem;
					margin-left: .01rem;
					// padding-left: 10px;
					width: 85%;
					> img {
						width: 13%;
					}
					> span {
						padding: 0 10px
					}
				}
			}
			> div:nth-child(2) {
				width: 80%;
				font-size: .7em;
				margin: 0 auto;
				text-align: left
			}
			> div:nth-child(3) {
				margin: .05rem auto;
			 	> div {
					// font-size: 1rem;
					width: 30%
				}
			}
			> .box{
				font-size: .7em;
				width: 80%;
				margin: 0 auto;
				text-align: left;
				color: white;
				background: #255e3f;
				border-radius: 15px;
				padding: 10px 20px;
				margin-bottom: 40px;
				> div {
					line-height: .05rem;
				}
			}

		}
		.help{
			// font-size: 1.8rem;
			width: 94%;
			margin: 0 auto;
			border-top: 1px solid transparent;
			background: url('../assets/background.png') no-repeat;
			background-size: 100%;
			height: 1.2rem;
			color: #1f5638;
			> h2 {
				font-size: 1em;
				margin-top: .05rem;
			}
			> .thanks{
				font-size: .7em;
				margin-top: .05rem;
			}
			> .personInfo {
				background: #e4d8bc;
				width: 87%;
				height: .1rem;
				margin: .03rem auto;
				border-radius: 11px;
				display: flex;
				display: -webkit-flex;
				justify-content: center;
				align-items:center;
				padding: .01rem 0;
				img {
					margin-left: 20px;
					width: 10%;
					border-radius: 40px;
				}
				> div:nth-child(2) {
					text-align: left;
					margin-left: 20px;
					width: 68%;
					display: inline-block;
					font-size: .7em;
				}
				> div:nth-child(3) {
					font-size: 1.1em;
					font-style:italic;
					padding-right: 8px;
					font-weight: 700;
					color: #1f5638;
					margin-right: .008rem;
					width: 25%;
				}
			}
			>.checkMore{
				font-size: .7em;
				color: #d2dabc;
				padding-bottom: 30px
			}
		}
		> .behind {
				background: url('../assets/bg-bottom.jpg') no-repeat;
				background-size: 100% 100%;
				.swiperBig {
					margin-top: 20px;
				}
				.swiperBig2 {
					height: 0.09rem;
					margin-left: 0 !important;
				}
				.swiperBox{
					.picture {
						position: relative;
						margin: .02rem;
						width: 80%;
						.achieve {
							position: absolute;
							top: -.02rem;
    					right: -.025rem;
							z-index: 99;
						}
						.giftName {
							font-size: .5em;
							padding: .01rem 0;
						}
						.integral {
							font-size: .5em;
						}
					}
					.picImg{
						position: relative;
						background-color: #f8ebc8;
						border-top-left-radius: 10px;
						border-top-right-radius: 10px;
						height: .15rem;
						line-height: .15rem;
						> img {
							width: 54%
						}
						> div {
							position: absolute;
							width: 100%;
							bottom: 0px;
							font-weight: 700px;
							padding: 8px 0;
							background: rgba(0, 0, 0, 0.5)
						}
					}
					img {
						// background-color: #f8ebc8;
						width: 70%;
						vertical-align: middle
						// padding: 0 20px 
					}
					.picture {
						padding: 2px;
						border-radius: 15px;
						display: inline-block;
						border: 2px solid #f8ebc8;
					}
				}
				
			}
	}
	.titlePic {
		width: 87%;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-55%, -100%);
	}
	.prog2 {
		padding: 3px;
		position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
		width: 80%;
		height: .04rem;
		border-radius: 30px;
		background: #236541;
		margin: 30px auto;
		float: left;
		border: 3px solid #3b9a66;
		> .load {
			color: #236541;
			margin-top: .05rem;
			font-weight: 700;
		}
	}

  .prog_line2{
		width: 0%;
		transition: all 01.25s ease-in-out;
   	height: .04rem;
    border-radius: 20px;
    /* background: red; */
    position: relative;
  }
  .prog_line2::after{
		display: block;
    content: "";
    height: .04rem;
    /* background: url(/img/active/report_progress1.png) no-repeat;
    background-size: 100% 100%; */
    background: #2fca83;
    position: absolute;
    left: 0;
    top: 0;
    animation: progshow 1s;
    width: 100%;
    border-radius: 15px;
  }
</style>



