document.write('<style>html{font-size:' + pageWidh + 'px;}</style>')
let body = document.getElementsByTagName('body')[0]
let html = document.getElementsByTagName('html')[0]
body.style.fontSize = 0.8 * 20 * (pageWidh / 320) + "px"
console.log(body.style.fontSize, 'body.style.fontSizebody.style.fontSize')
